body{padding:0;margin:0;background-color:rgba(0,0,0,0.1);font-family:"Roboto","Titillium Web"}h1{color:#3078d6;font-size:3rem;text-align:center}h2{color:#527c0f;font-size:1.5rem;text-align:center}.container{padding:10px;width:380px;margin:0 auto}.header{width:380px;height:200px;background-image:url("../img/dsc.png");background-position:0 58%;background-size:100% 200%;background-repeat:no-repeat;border-radius:15px 15px 0 0;position:relative}.clear{width:30px;height:30px;position:absolute;right:20px;top:20px}.clear i{font-size:30px;color:#dc2d2d}.clear i:hover{cursor:pointer;text-shadow:1px 3px 5px #000;-webkit-transform:rotate(45deg);transform:rotate(45deg)}#date{position:absolute;bottom:10px;left:10px;color:#000;font-size:25px;font-family:"Roboto","Titillium Web"}.content{width:380px;height:350px;max-height:350px;background-color:#fff;overflow:auto}.content::-webkit-scrollbar{display:none}.content ul{padding:0;margin:0}.item{width:380px;height:45px;min-height:45px;position:relative;border-bottom:1px solid rgba(0,0,0,0.2);list-style:none;padding:0;margin:0}.item i.co{position:absolute;font-size:25px;padding-left:5px;left:15px;top:10px}.item i.co:hover{cursor:pointer}.fa-check-circle{color:#527c0f}.fa-trash-o{color:#f5bf6e}.item p.text{position:absolute;padding:0;margin:0;font-size:20px;left:50px;top:10px;background-color:#FFF;max-width:285px}.lineThrough{text-decoration:line-through;color:#ccc}.item i.de{position:absolute;font-size:25px;right:15px;top:10px}.item i.de:hover{color:#af0000;cursor:pointer}.add-to-do{position:relative;width:360px;height:40px;background-color:#FFF;padding:10px;border-top:1px solid rgba(0,0,0,0.3)}.add-to-do i{position:absolute;font-size:40px;color:#3078d6}.add-to-do input{position:absolute;left:50px;height:35px;width:310px;background-color:transparent;border:none;font-size:20px;padding-left:10px}.add-to-do input::-webkit-input-placeholder{color:#3078d6;font-family:"Roboto","Titillium Web";font-size:20px}.add-to-do input::-moz-placeholder{color:#3078d6;font-family:"Roboto","Titillium Web";font-size:20px}.add-to-do input:-ms-input-placeholder{color:#3078d6;font-family:"Roboto","Titillium Web";font-size:20px}.add-to-do input:-moz-placeholder{color:#3078d6;font-family:"Roboto","Titillium Web";font-size:20px}
